<h2 id="mac-os" tabindex="-1"><a class="header-anchor" href="#mac-os" aria-hidden="true">#</a> Mac OS</h2>
<h3 id="homebrew" tabindex="-1"><a class="header-anchor" href="#homebrew" aria-hidden="true">#</a> Homebrew</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>brew <span class="token function">install</span> <span class="token function">yarn</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="upgrade-yarn" tabindex="-1"><a class="header-anchor" href="#upgrade-yarn" aria-hidden="true">#</a> Upgrade Yarn</h3>
<p>如果有新版本yarn会有警告，使用下面命令进行升级</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>brew upgrade <span class="token function">yarn</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>测试现在的yarn版本</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">yarn</span> --version
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>